	module.exports = {"data":{"markdownRemark":{"html":"<h2>اتوبوس Metrobus</h2>\n<p>حمل و نقل یا Transportation در شهر St. John’s به 4 روش قابل انجام هست. که اولین مورد و پرکاربرد ترین برای دانشجویان استفاده از سیستم اتوبوس رانی می باشد.</p>\n<p>اسم سیستم اتوبوس رانی سنت جانز metro bus هست و یک ترمینال اصلی در دانشگاه وجود داره که تقریبا به همه جای شهر حمل و نقل رو امکان پذیر می‌کند. اتوبوس ها طبق برنامه موجود در سایت metrobus.com در 22 مسیر مختلف شهر را پوشش می دهند. برنامه اتوبوس در پاییز و زمستان با بهار و تابستان متفاوت هست که در سایت هر دو ارائه شده. البته در کنار ایستگاه های اصلی شماره خطوط اتوبوس که از ایستگاه عبور می‌کنند و زمان رسیدن اتوبوس ذکر شده!</p>\n<h2>مهمترین خط ها</h2>\n<p>برای حمل نقل به شرح زیر می باشد:\nRoute 1  که از Village Mall به Marine Institute میره و بالعکس و در بین مسیر از Memorial University Center عبور می‌کند. در صورتیکه در آپارتمان های Valleview، Columbus Terrace، Grenfell Court و Kelly’s Brook سکونت داشته باشید در فاصله چند قدمی منزلتون ایستگاه اتوبوس برای خط یک وجود داره.\nRoute 10 که از Downtown تا Avalon Mall و بالعکس حرکت می‌کند. در طول مسیر از Health Center بیمارستان و Memorial University Centerعبور می‌کند.\nRoute 14 که از Memorial University Center تا Airport میره و در بین مسیر از Plaza Medical Group هم عبور می‌کند. Plaza Medical Group شامل مطب دو پزشک ایرانی در سنت جانز می باشد.  </p>\n<h3>هزینه</h3>\n<p>هزینه بلیط اتوبوس برای هر بار سوار شدن ۲/۵ دلار هست در صورتیکه کارت metro bus از فروشگاه Attic از طبقه سوم UC تهیه کنید هرینه ۱۰ دلار برای خود کارت و ۱۰ سفر معادل ۲۲/۵ دلار پرداخت خواهید کرد. این کارت قابل شارژ هست. در صورت تعویض اتوبوس، در یک ساعت اول که سوار اتوبوس شدید بدون هزینه مجدد قادر به سوار شدن هستید. در صورتیکه خیلی از اتوبوس استفاده می‌کنید یعنی بیشتر از ۴۰ سفر در ماه حتما 30 Day pass یا Semester Pass روی کارتتون شارژ کنید چون قیمتش خیلی مقرون به صرفه تره.\nاگر کارت MetroBus ندارید حتما پول خرد همراهتون باشه چون هر مبلغی پرداخت کنید، راننده بهتون باقی پول رو پس نخواهد داد. این وظیفه مسافر هست که پول خرد اماده داشته باشه.\nدر صورتیکه قصد دارید ظرف مدت یک ساعت مجددا سوار اتوبوس بشید و پول نقد پرداخت کردید برای سوار شدن ، حتما از راننده درخواست Transfer ticket کنید. با ارائه رسید به اتوبوس بعدی ظرف یک ساعت ازتون هزینه نخواهند گرفت. </p>\n<h3>نکات</h3>\n<ul>\n<li>هم خط رفت و هم برگشت در دانشگاه در یک نقطه توقف دارند پس اگر از جهت حرکت اتوبوس مطمئن نیستید حتما از راننده بپرسید که آیا به مقصد مد نظرتون میره یا نه</li>\n<li>حتما Google Maps روی موبایلتون نصب کنید و با اینترنت خطوط اتوبوس، زمان رسیدن به ایستگاه مورد نظرتون رو چک کنید.</li>\n<li>سعی کنید ۳-۴ دقیقه قبل از رسیدن اتوبوس تو ایستگاه حضور داشته باشید چون اتوبوس برای مسافرانی که دیر میرسند یا خارج ایستگاه هستند توقف نمی‌کند.</li>\n<li>اگر قصد پیاده شدن در ایستگاه بعدی رو دارید به محض رد شدن اتوبوس از ایستگاه قبلی سیم زنگ اتوبوس رو بکشید. زیرا اگه خیلی نزدیک ایستگاه مد نظر زنگ رو به صدا در بیارید اتوبوس توقف نمی‌کند بعلت سرعت بالا و امنیت مسافران! </li>\n</ul>\n<h2>Shuttle Battery</h2>\n<p>جهت حمل و نقل به خوابگاه battery که نزدیک Signall Hill  هست می‌توانید از شاتل مربوطه استفاده کنید.\nکه از ۷:۳۰ صبح تا ۱۰:۳۰ شب به دانشجوها سرویس می‌دهد و در سرویس شب طی مسیر یک ساعت توقف در Dominion واقع در lake avenue دارد. ساعات حرکت و محل توقف شاتل در لینک زیر کامل ارائه شده:\n<a href=\"http://www.mun.ca/sustain/initiatives/Shuttle_-_Final_Schedule.pdf\">http://www.mun.ca/sustain/initiatives/Shuttle<em>-</em>Final_Schedule.pdf</a></p>\n<h2>تاکسی</h2>\n<p>برای استفاده از تاکسی در سنت جانز حتما باید با تلفن رزرو کنید و با علامت دادن در خیابان براتون توقف نمی‌کنن.\nپس از تماس فقط آدرسی که خودتون هستید رو بیان می‌کنید و تاکسی براتون میاد. پس مثل ایران لزومی نداره مقصد رو بگید. شما هر جا قصد دارید برید داخل تاکسی به راننده اعلام می‌کنید.</p>\n<p>نام شرکت های معروف تاکسی رانی در سنت جانز و شماره تماس  آنها به شرح زیر است:</p>\n<ul>\n<li>Newfoundland Cab (709) 744-4444</li>\n<li>Jiffy Cab (709) 722-2222</li>\n<li>City wide taxi (709) 722-7777</li>\n</ul>\n<p>در فرودگاه همه مسافرانی که درخواست تاکسی دارند صف می کشند و تاکسی در فرودگاه به نوبت بهشون سرویس می‌دهد.\nدفعه اول می‌خواهید برسید فرودگاه حتما شماره تلفن و آدرس جایی که می‌خواهید برید رو همراه داشته باشید حتی اگر باید پیکاپ بشید! اینجوری اگر پروازتون تاخیر داشته باشد  یا مجبور باشید پرواز عوض کنید نگران رسیدن به محل اقامتتون نخواهید بود.</p>\n<p>در صورتیکه بار اول از فرودگاه می‌خواهید به محل اقامتتون برید حتما از راننده درخواست رسید کنید  وبا ارابه رسید به  Internationalization Office می‌توانید هزینه رو دریافت کنید ولی بهتر این هست که درخواست Airport Greeting کنید از Internationalization Office.</p>\n<p>در صورتیکه بازه زمان رسیدنتون به سنت جانز مطابق اطلاعات ارائه شده در Internationalization office باشه می‌توانید درخواست  Pickup  و Greeting  مجانی کنید. که مراحلش در لینک زیر ارائه شده‌است:\n<a href=\"https://www.mun.ca/international/students/beforearrival/#AirportGreeting\">https://www.mun.ca/international/students/beforearrival/#AirportGreeting</a></p>\n<h2>Car rental</h2>\n<p>رزرو ماشین از طریق شرکت <a href=\"https://www.enterprise.ca/en/home.html\">Enterprise</a> قابل انجام هست.\nجهت اجارء ماشین، ابتدا به صورت آنلاین برای تاریخ مورد نظر نوع ماشین و مرکزی که می‌خواهید ماشین رو تحویل بگیرید مشخص می‌کنید و سپس روز رزرواسیون می‌توانید با یک تماس تلفنی به مرکز انتخابی درخواست pickup  بدید.\nفقط توجه داشته باشید که برای رزرو ماشین باید حتما گواهینامه ترجمه شده ، گواهینامه بین المللی ،گواهینامه کانادایی  یا  گواهینامه آمریکایی داشته باشید.\nپرداخت هزینه فقط از طریق کردیت کارد انجام پذیره و اسم صاحب کردیت کارد و اسم صاحب گواهینامه بایستی یکسان باشد. پس نمی‌توانید بخاطر استفاده از خدمات insurance برای اجاره ماشین از کردیت کار دوستتون یا حتی همسرتون استفاده کنید.\nگواهینامه ترجمه شده یا بین المللی شما فقط تا سه ماه از ورود شما به کانادا معتبر هست و پس از آن حتما باید اقدام به دریافت گواهینامه Newfoundland  بکنید.</p>\n<p>گواهینامه کانادایی learners برای اجاره ماشین قابل استفاده نیست.</p>","frontmatter":{"path":"/guides/transportation","title":"حمل و نقل شهری","cover":"http://www.barp.ca/bus/sjtc/sjtc9236.jpg","direction":"rtl"}},"allMarkdownRemark":{"edges":[{"node":{"frontmatter":{"title":"اسکان و محل اقامت","path":"/guides/accommodation"}}},{"node":{"frontmatter":{"title":"بانک‌های کانادا و نحوهٔ افتتاح حساب بانکی","path":"/guides/banks"}}},{"node":{"frontmatter":{"title":"ورود به سینت‌جانز","path":"/guides/coming-to-st-johns"}}},{"node":{"frontmatter":{"title":"بیمهٔ درمانی","path":"/guides/health-insurance"}}},{"node":{"frontmatter":{"title":"حمل و نقل شهری","path":"/guides/transportation"}}}]}},"pathContext":{}}

